body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display:flex;
  text-align: center;
  background-color: #282c34;
  color: white;
  min-height: 100vh;
}
.Buffer {
  flex: 1 1;
}

/* CALENDAR AREA */

.FirstColumnNumber{
  height:2.5%;
}

.Calendar{
  flex: 1 1;
  display:flex;
}

.CalendarColumn{
  display:flex;
  flex-direction: column;
}

.MoodRating{
  border-radius: 50%;
  height:15px;
  width:15px;
  color: white;
}

/* STATS AREA */

.Stats{
  display: flex;
  flex-direction: row;
}

.PieGraph{
  flex: 1 1;
}

.ColumnGraph{
  flex: 1 1;
}
